import validate from 'validate.js';
// import addDiv from '/addDiv';
import '../App.css';
import React, {useState} from 'react';

import Clip from './../asset/Paper-clip_icon-icons.com_51979.svg';


const FormStyle = styled.form`
  background-color: white; 
  margin: auto;
  width: 600px;
  height: 580px;
  display: flex; 
  flex-direction: column;
  align-items: center; 
  border: 1px solid #efefef; 
  box-shadow: 0 1px 1px rgba(0,0,0,0.15)
  padding: 30px; 
`;

const H1 = styled.h1(
    {
        display: "flex",
        fontSize: 20,
        color: "black",
        alignItems: "flex-start",
        // marginRight: "55%",
        // marginTop: "20px",
        fontFamily: 'Bellota Text',

    },
);


const P = styled.p(
    {
        fontFamily: 'Bellota Text',
        fontSize: 13,
        color: "grey",
    }
);

// const FormStyle = styled.form({
//   height: "100%",
// })

const MainDiv = styled.div({
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    height: "100%",
    width: "100%",
});

const FormMain = styled.form({

    // backgroundColor: "lightblue",
    padding: "10px",
    height: "250px",
    width: "584px",


});

const FromWhom = styled.div({
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
});

const ErrorMessage = styled.div({
    color: 'red'
});

const FromWhom__label = styled.div({
    fontFamily: 'Bellota Text',
    fontSize: "13px",

});

const FromWhom__inputs = styled.div({
    display: "flex",
    width: "auto",
});


const ToWhom = styled.div({
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
});

const ToWhom__label = styled.div({
    marginTop: "34px",
    fontFamily: 'Bellota Text',
    fontSize: "13px",
});

const ToWhom__inputs = styled.div({
    display: "flex",
    flexDirection: "row",
    width: "auto",
});


const MessageTopic = styled.div({
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    width: "99%",
});

const MessageTopic__label = styled.div({
    marginTop: "16px",
    fontFamily: 'Bellota Text',
    fontSize: "13px",
    width: "99%",
});

const MessageTopic__input = styled.input({
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    width: "100%",
    height: "25px",
    border: "1px solid #ece5e5",
    borderRadius: "3px 3px 3px 3px",
});
const MessageTopic__textArea = styled.textarea({
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    width: "100%",
    height: "25px",
    border: "1px solid #ece5e5",
    borderRadius: "3px 3px 3px 3px",
    resize: 'none',
});


const MessageContent = styled.div({
    width: "99%",
});

const MessageContent__label = styled.div({
    marginTop: "16px",
    fontFamily: 'Bellota Text',
    fontSize: "13px",
});
const InputFile = styled.input({
    width: "0.1px",
    height: '0.1px',
    opacity: '0',
    overflow: 'hidden',
    position: 'absolute',
    border: "none",
    marginTop: '-10px'
    /*z-index: "-1",*/
});
const LabelForFiles = styled.label({
    width: "180px",
    height: "50px",
    cursor: "pointer",
    display: "block",
    font: "14px/50px Tahoma",
    transition: "all 0.18s ease-in-out",
    color: "#2579f8",
    textAlign: "center"
   /* &:befor{
        background: "url('./asset/Paper-clip_icon-icons.com_51979.svg') no-repeat"*/
});

/*const MessageContent__input = styled.input({
    // marginTop: "10px",
    // backgroundColor: "#2579f8",
    // height: "33px",
})*/


const AttachedFiles = styled.input({
    marginTop: "10px",
});

const AttachFile = styled.input({
    height: "33px",
    width: "100px",
    fontSize: "1px",
    backgroundColor: "#2579f8",
    padding: "0",
    border: "none",
    font: "inherit",
    color: "white",
    fontFamily: 'Bellota Text',
    borderRadius: "4px 4px 4px 4px",
    display: "block",
    marginTop: "10px",
});

const SendButton = styled.button({
    height: "33px",
    width: "100px",
    fontSize: "1px",
    backgroundColor: "#2579f8",
    padding: "0",
    border: "none",
    font: "inherit",
    color: "white",
    fontFamily: 'Bellota Text',
    borderRadius: "4px 4px 4px 4px",
    display: "block",
    marginTop: "10px",
});
const ImgCLip = styled.img({
    width: '15px',
    height: '15px',
    paddingRight: '5px',
    transform: 'rotate(90deg)'
});


const Send = styled.div({});


function Form() {
    const [files, setFiles] = useState('');
    const [senderEmail, setSenderEmail] = useState('');
    const [receiverEmail, setEmailReceiver] = useState('');
    const [toggleSubmit, setToggleSubmit] = useState(true);


    let filesSize = 0;



    let constraints = {
        email: {
            email: true,
        },
    };

    let setSenderEmailInState = (e) => {
        setSenderEmail(e.currentTarget.value);
        console.log(document.getElementsByName('senderEmail')['0'].value);
        if (document.getElementsByName('senderEmail')['0'].value && document.getElementsByName('receiverEmail')['0'].value) {
            setToggleSubmit(false)
        }
    };
    let setReceiverEmailInState = (e) => {
        setEmailReceiver(e.currentTarget.value);
        // if (document.getElementsByName('senderEmail').value && document.getElementsByName('receiverEmail')['0'].value) {
        //     setToggleSubmit(false)
        // }
    };

    function validateEmail(e) {
        let validateToggle = true;
        let result = validate.isEmpty(e.currentTarget.value);
        let errorTag = document.getElementById(e.target.name + 'Error');
        if (result) {
            errorTag.innerHTML = 'Email не может быть пустым';
            validateToggle = false;
        }
        let emailValidate = validate({email: e.currentTarget.value}, constraints);
        if (emailValidate && validateToggle) {
            errorTag.innerHTML = 'Введите правильный Email';
            validateToggle = false;

        }
        if (validateToggle) {
            errorTag.innerHTML = '';
            if(receiverEmail && senderEmail){
                setToggleSubmit(false)
            }
        }
    }

    function validateName(e) {
        let result = validate.isEmpty(e.currentTarget.value);
        let errorTag = document.getElementById(e.target.name + 'Error');
        if (result) {
            errorTag.innerHTML = 'Поле имени не может быть пустым';
        } else {

            errorTag.innerHTML = '';
        }
    }




    function fileChangedHandler(event) {
        let file_size = event.target.files[0].size;
        let file_name = event.target.files[0].name;
        let file_type = event.target.files[0].type;
        filesSize += file_size;


        if (file_size > 5000000 && filesSize > 20000000) {
            return alert("Размер файла превышает допустимое значение")
        } else {
            setFiles(files + file_name + ",  ")
        }

        console.log(file_size);
        console.log(file_name);
        console.log(file_type);
    }



    return (
        <FormStyle>
            <MainDiv>
                <FormMain action="/upload" method="POST"
                          enctype="multipart/form-data" name='mainForm'>
                    <H1>Отправлялка сообщений</H1>

                    <FromWhom>
                        <FromWhom__label>От кого</FromWhom__label>
                        <FromWhom__inputs>
                            <label>
                                <MessageTopic__input type="text" style={{width: "286px"}} placeholder={'Имя'}
                                                     name={'senderName'} onBlur={validateName}/>
                                <ErrorMessage id='senderNameError'/>
                            </label>
                            <div>
                                <label>
                                    <MessageTopic__input type="Email" style={{width: "286px"}} placeholder={'Email'}
                                                         onChange={setSenderEmailInState} value={senderEmail}
                                                         onBlur={validateEmail} name={'senderEmail'}

                                    />
                                    <ErrorMessage id='senderEmailError'/>
                                </label>
                            </div>
                        </FromWhom__inputs>
                    </FromWhom>

                    <ToWhom>
                        <ToWhom__label>Кому</ToWhom__label>
                        <ToWhom__inputs>
                            <div>
                                <label>
                                    <MessageTopic__input type="text" style={{width: "286px"}} placeholder={'Имя'}
                                                         name={'receiverName'} onBlur={validateName}/>
                                    <ErrorMessage id='receiverNameError'/>
                                </label>
                            </div>
                            <div>
                                <label>
                                    <MessageTopic__input type="Email" style={{width: "286px"}} placeholder={'Email'}
                                                         name={'receiverEmail'} onChange={setReceiverEmailInState}
                                                         value={receiverEmail} onBlur={validateEmail}/>
                                    <ErrorMessage id='receiverEmailError'/>
                                </label>
                            </div>
                        </ToWhom__inputs>
                    </ToWhom>

                    <MessageTopic>
                        <MessageTopic__label>Тема письма</MessageTopic__label>
                        <div>
                            <label>
                                <MessageTopic__input type="text" style={{width: "100%"}}/>
                            </label>
                        </div>
                    </MessageTopic>

                    <MessageContent>
                        <MessageContent__label>Сообщение</MessageContent__label>
                        <label>
                            <MessageTopic__textArea type="text" style={{height: "100px", width: "100%"}}/>
                        </label>
                    </MessageContent>

                    <div>
                        <p>{files}</p>
                        <div className="dada">
                            <LabelForFiles htmlFor="myfile">
                                <ImgCLip src={Clip} alt="img"/>
                                Прикрепить файл
                            </LabelForFiles>
                            <InputFile name="files" type="file" multiple id="myfile"
                                       onChange={fileChangedHandler}/>

                        </div>
                    </div>

                    <SendButton disabled={toggleSubmit} classname="submitBtn">Отправить</SendButton>

                    <Send id="e-fileinfo"/>

                </FormMain>

            </MainDiv>
        </FormStyle>
    );
}

export default Form;



